const description = `ta-text-area is a customizable text area component designed for use in forms and user input fields. It supports dynamic customization of attributes such as label, placeholder, and validation properties (e.g., required, minlength, maxlength). The component allows users to control the number of visible lines with the rows property, making it adaptable for varying content lengths. Additionally, it includes support for descriptions, focus management, and input change handling, ensuring ease of use and flexibility for different types of text input requirements. Whether for comments, messages, or other multiline inputs, ta-text-area is a versatile solution for capturing user input in a structured way.`;

const Template: StoryFn = (args) => {
  const inputChangeHandler = (event: Event) => {
    const inputTarget = event.target as HTMLInputElement;
    const outputElement = document.getElementById('entered-text');
    if (outputElement) outputElement.innerHTML = inputTarget.value;
  };
  return html`
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column>
        <ta-text-area
          id="${args.id}"
          .label="${args.label}"
          ?disabled="${args.disabled}"
          .description="${args.description}"
          .value="${args.value}"
          .placeholder="${args.placeholder}"
          .required="${args.required}"
          .minlength="${args.minlength}"
          .maxlength="${args.maxlength}"
          .rows="${args.rows}"
          @input="${(e: Event) => inputChangeHandler(e)}"></ta-text-area>
        <p>Entered text: <span id="entered-text"></span></p>
      </ta-grid-column>
      <ta-grid-column
        ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
      >
      <ta-grid-column><div class="story-description">${description}</div></ta-grid-column>
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-input',
  label: 'Label',
  disabled: false,
  description: 'Some description',
  value: '',
  placeholder: 'Enter text',
  required: false,
  minlength: 0,
  maxlength: 524288,
  rows: 4,
};
